import {Injectable} from '@angular/core';
import {ITableData} from '../Models/table-data';
import {DataService} from './data.service';
import {IMeasuring} from '../Models/measuring';

@Injectable({
    providedIn: 'root'
})
export class MeasuringUnitsService {


    Measuring_units = {} as IMeasuring;

    constructor(public dataService: DataService) {
    }


    getMeasuring_units() {
        return this.dataService.get('measuring_units');
    }


    AddMeasuring_units() {
        return this.dataService.post('admin/measuring_units', this.Measuring_units);
    }

    deleteMeasuring_units(id: number) {
        return this.dataService.delete('admin/measuring_units' + '/', id);

    }

    editMeasuring_units(id: number) {
        return this.dataService.update('admin/measuring_units' + '/' + id, this.Measuring_units);

    }
}
